<template>
    <div class="file-watch-wrap">
        <div style="width: 280px;border-right: 1px solid #dfe1e6;">
            <add-comp @handleAdd="addFile" @handleInput="searchFile"> </add-comp>
            <div style="height: calc(100% - 60px)">
                <scroll-group v-model="active"  :enableDelete="true" @change="handleClick" @delete="handleDel">
                    <scroll-item v-for="(item,index) in fileData"
                               :itemKey="item.key"
                               :index="index"
                               :source="item" >
                        <task-item :data="item" :index="index" @handleCopy="handleCopy"></task-item>
                    </scroll-item>
                </scroll-group>
            </div>
        </div>
        <transition name="fade-transform" mode="out-in">
            <div class="content-view-wrap" v-show="showContent">
                <task-content
                        :loggerData="loggerData"
                        :form="currentViewData"
                        :state="state"
                        @handleAdd="handleAdd"
                        @handleUpdate="handleUpdate"></task-content>
            </div>
        </transition>
        <el-dialog
                title="删除提示"
                :visible.sync="visibleDialog"
                width="30%">
            <span>确定删除该任务吗？</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="visibleDialog = false">取 消</el-button>
                <el-button type="primary" @click="deleteFile">确 定</el-button>
            </span>
        </el-dialog>
    </div>

</template>

<script>
    import RecycleList from '../../../components/recycle-view/main'
    import AddComp from '../../../components/business/AddComp/AddComp'
    import ScrollGroup from '../../../components/Scroll/scroll-group'
    import ScrollItem from '../../../components/Scroll/scroll-item'
    import TaskItem from './item'
    import TaskContent from './content'


    import {deepClone} from '../../../utils/common'

    import {fileMixin} from "../../../mixin/fileMixin";
    import {StompClient} from "ht-common/src";
    import config from "../../config.js";

    const listUrl = 'jobDetails/getAllJob'
    const deleteUrl = 'jobDetails/deleteJob'
    // import {getConfig} from "./utils/config";
    let stompClient;

    export default {
        name: "TaskSchedule",
        mixins:[fileMixin],
        data() {
            return {
                listUrl:listUrl,
                deleteUrl:deleteUrl,
                itemKey:'key',
                // showContent: true,
                // subscriptionStatus:null,        // 任务状态
                // subscriptionLog:null,           // 任务日志
                loggerData:null,
            }
        },
        mounted() {
            this.getFileList().then(res=>{
                if(res){
                    this.fileData = res
                    this.allFileData = res
                    if(this.allFileData.length===0){
                      this.addFile()
                    }
                    this.setCurrentViewData()
                }
            })
            this.initWebSocket()
        },
        beforeDestroy() {
            this.disconnect()
        },
        methods:{
            searchFile(value){
                this.fileData = []
                for(let i=0,l=this.allFileData.length;i<l;i++){
                    let item = this.allFileData[i]
                    let flag =  item.alias.indexOf(value)>-1 ||  item.jobName.indexOf(value)>-1
                    if(flag){
                        this.fileData.push(item)
                    }
                }
            },
            // 点击加号按钮
            addFile(){
                this.switchViewAnimate()
                this.currentViewData = deepClone({isUpdate:true})
                // this.state = 1
                this.state = this.handleType['add']
            },
            handleCommand(data){
                this.currentViewData = deepClone({transmissionMode:data.value})
                this.state = 1
            },
            handleCopy(data){
                let _data  = deepClone(data)
                _data.isUpdate = true
                _data.jobName=''
                _data.alias=''
                _data.description=''
                this.currentViewData = _data
                // this.state = 1
                this.state = this.handleType['copy']
                this.switchViewAnimate()
            },
            initWebSocket() {
              stompClient= new StompClient(config.taskWebsocketUrl,{debug:config.websocketDebug});
                stompClient.connect({}, (frame) => {
                    this.subscribeStatus()
                    this.subscribeLogger()
                })
            },
            disconnect() {
                if (stompClient != null) {
                    stompClient.disconnect();
                }
            },
            unsubscribeStatus(url) {
              stompClient.unsubscribe(url);
            },
            subscribeStatus() {
                stompClient.subscribe('/job/status', (event) => {
                    let data = JSON.parse(event.body);
                    for(let i=0,l = this.fileData.length;i<l;i++){
                        let item = this.fileData[i]
                        if(item.key===data.key){
                            item.isUpdate = false // 用于判断修改时间和状态不更新右侧的内容
                            item.status = data.status;
                            if(data.nextFireTime){
                                item.nextFireTime = data.nextFireTime
                            }
                            this.$forceUpdate()
                            break
                        }
                    }
                })
            },
            subscribeLogger(){
                stompClient.subscribe('/job/task', (event) => {
                    let data = JSON.parse(event.body);
                    if(this.currentViewData.key === data.jobKey){
                        this.loggerData = data
                    }
                })
            }
        },
        components: {
            RecycleList,
            TaskContent,
            TaskItem,
            AddComp,
            ScrollGroup,
            ScrollItem
        }
    }
</script>

<style scoped lang="scss">
    @import "../../../styles/views/fileView";
</style>

