<template>
  <div class="editor-input-wrap">
    <span id="label" ref="label">
<!--      <svg-icon icon-class="require" :class="[required?'required':'not-required']"/>-->
      {{ label }}
      ：</span>
    <el-input :style="inputWidth"  :disabled="disabled" type="text" v-model="selfValue"
              @input="change" size="small"></el-input>
  </div>
</template>

<script>
import {getByteLen} from '@/utils/common'
export default {
  name: "EditorInput",
  props: {
    value: null,
    label: null,
    required: {
      type: Boolean,
      default: false
    },
    disabled: false
  },
  data() {
    return {
      edit: false,

    }
  },
  mounted() {
  },
  methods: {
    change() {
      this.edit = false
      this.$emit('change', this.target)
    },
    setInputSize() {
      this.size = (this.selfValue.length) * 10 + 40;
    },

  },
  watch: {
    // selfValue(newVal, oldVal) {
    //   if (newVal) {
    //     this.setInputSize()
    //   }
    // }
  },
  computed: {
    inputWidth() {
      let len =getByteLen(this.label)*9+10+'px'
      return {
        width: `calc(100% - ${len})`
      }
    },
    selfValue: {
      get() {
        return this.value
      },
      set(newVal) {
        this.$emit('update:value', newVal)
      }
    },
    size: {
      get() {
        return this.value ? (this.value.length) * 10 + 40 : 60
      },
      set(newVal) {
      }
    },

  }
}
</script>

<style scoped lang="scss">
.editor-input-wrap {
  .input {
    display: inline-block;
    background: none;
    border: none;
    outline: none;
    /*width: calc(100% - 100px);*/
    /*width: 90px;*/
    height: 20px;
    padding: 0 3px;
    border-bottom: 1px solid #cdcdcd;
    font-size: 14px;
  }

  .required {
    color: #f56c6c;
  }

  .not-required {
    color: transparent;
  }

  .value {
    border-bottom: 1px solid #cdcdcd;
    display: inline-block;
    width: 90px;
    padding: 0 3px;
    height: 20px;
  }

  .icon {
    cursor: pointer;
  }

  .editor {
    color: #08abf9
  }

  .success {
    color: #67c23a
  }
}

</style>
<style lang="scss">
.editor-input-wrap {
  .el-input__inner {
    border-radius: 0;
    border: none;
    border-bottom: 1px solid #cdcdcd;
  }
}
</style>
