<template>
    <div class="add-wrap">
        <el-input
                style="width:calc(100% - 60px);margin-right: 10px"
                size="small"
                placeholder="请输入内容"
                @input="handleInput"
                v-model="search">
            <i slot="suffix" class="el-input__icon el-icon-search"></i>
        </el-input>
        <span class="delete-item" @click="handleAdd"><i class="el-icon-circle-plus-outline add-icon"></i></span>
    </div>
</template>

<script>
    export default {
        name: "AddComp",
        data(){
            return{
                search:''
            }
        },
        methods:{
            handleAdd(){
                this.$emit("handleAdd")
            },
            handleInput(){
                this.$emit('handleInput',this.search)
            }
        }
    }
</script>

<style scoped lang="scss">
    .add-wrap {
        .delete-item {
            display: inline-block;
            width: 50px;
            background: #26de9a;
            height: 30px;
            line-height: 30px;
            text-align: center;
            border-radius: 4px;
            color: #fff;
        }
        .add-icon {
            font-size: 20px;
            vertical-align: middle;
            margin-right: 5px;
            cursor: pointer;
        }
        .el-input__inner {
            border: none;
            border-bottom: 1px solid #ccc;
            border-radius: 0;
            background: transparent;
        }
    }


</style>
