export const itemMixin = {
    data() {
        return {
            visible:false
        }
    },
    props: {
        data: {
            type: Object,
            require: true
        },
        index:{
            require: true
        }
    },
    methods: {
        handleSuccess(){
            this.visible = false
            this.$emit('handleCopy',this.data)
        },
        message(content,type){
            this.$message({
                message: content || '成功了！',
                type: type|| 'success'
            })
        }
    }
}
