<template>
    <div class="scroll-item-wrap"  :class="[isActive?'active':'']">
        <div class="item-content"
             @click="handleClick"
             @mouseover="handleMouseOver"
             @mouseout="handleMouseOut">
            <slot></slot>
            <span class=" el-icon-close del"  slot="reference"  v-show="isEnableDelete&&showDelete" @click.stop.prevent="handleDel"></span>
        </div>
    </div>
</template>

<script>
    import emitter from '../../mixin/emitter'
    export default {
        name: 'ScrollItem',
        componentName:'ScrollItem',
        mixins:[emitter],
        data(){
            return{
                showDelete:false,
                visible:false,
            }
        },
        props: {
            source: {
                type: Object,
                default() {
                    return {}
                }
            },
            index:'',
            itemKey:'',
        },

        inject: ['scroller'],
        methods:{
            handleClick(){
                // this.$emit('handleClick',this.source)
                // this.$forceUpdate()
                this.dispatch('ScrollGroup', 'item-click', this);
            },
            handleDel(){
                this.visible = false
                this.dispatch('ScrollGroup', 'item-delete', this.source);
                // this.$emit('handleDel',this.source)
            },
            handleMouseOver(){
                this.showDelete=true
            },
            handleMouseOut(){
                this.showDelete = false
            },
        },
        computed:{
            isActive() {
                if(this.scroller.activeKeys){
                    return this.scroller.activeKeys.indexOf(this.itemKey) > -1;
                }
            },
            isEnableDelete(){
                return this.scroller.enableDelete
            }
        },
    }
</script>

<style scoped lang="scss">
    @import "../../styles/views/scrollItem";
</style>

