import { deepClone } from '../utils/common'
import {handleType} from '../package/dataConfig'
export const fileMixin ={
    data() {
        return {
            fileData: [],
            allFileData:[],
            currentViewData:{},
            handleType:handleType,
            state:handleType['scan'],
            showContent: true,
            visibleDialog:false,
            deleteFileData:{},
            active:'',          // 当前选中item
        }
    },
    mounted() {

    },
    methods: {
        getFileList() {
            return new Promise(((resolve, reject) => {
                this.request.get(this.listUrl)
                    .then(res=>{
                        if(res.data.code===200){
                            resolve(res.data.data)
                        }
                    }).catch(err=>{
                    console.log(err)
                })
            }))
        },
        create(){
            this.currentViewData = {}
            this.state = handleType['add'] // 1：创建 0 编辑
        },
        // 添加任务成功
        handleAdd(data){
            this.allFileData.push(data)
            this.state = handleType['scan']
            data.isUpdate = true;
            this.currentViewData=  data
            this.active = this.currentViewData[this.itemKey]
        },
        handleUpdate(data){
            for(let i=0,l=this.allFileData.length;i<l;i++){
                let item = this.allFileData[i]
                if(item[this.itemKey] === data[this.itemKey]){
                    this.allFileData.splice(i,1)
                    this.allFileData.push(data)
                    break;
                }
            }
        },
        handleDel(data){
            this.visibleDialog = true
            this.deleteFileData = data
        },
        deleteFile(){
            this.request.postJson(this.deleteUrl,this.deleteFileData)
                .then(res=>{
                    if(res.data.code===200){
                        this.removeFile(this.deleteFileData)
                        this.message('删除成功！')
                    }
                    this.visibleDialog = false
                }).catch(err=>{
                this.message('删除失败！','error')
                this.visibleDialog = false
            })
        },
        removeFile(data){
            for(let i=0,l=this.allFileData.length;i<l;i++){
                let item = this.allFileData[i]
                if(data[this.itemKey]===item[this.itemKey]){
                    this.allFileData.splice(i,1)
                    // this.fileData.splice(i,1)
                    this.setCurrentViewData()
                    break
                }
            }
        },
        handleClick(data) {
            if(data[0]===this.currentViewData[this.itemKey]&&this.state===this.handleType['scan']){
                return
            }
            for(let i=0,l=this.fileData.length;i<l;i++){
                let item = this.fileData[i]
                if(item[this.itemKey]===data[0]){
                    item.isUpdate = true
                    this.currentViewData = item
                    break;
                }
            }
            this.state = this.handleType['scan']
            this.switchViewAnimate()
        },
        setCurrentViewData(){
            if(this.fileData.length>0){
                this.fileData[0].isUpdate = true
                this.currentViewData = this.fileData[0]
            }else {
                this.currentViewData = {}
            }
            this.active = this.currentViewData[this.itemKey]
        },
        switchViewAnimate(){
            this.showContent=false;
            setTimeout(()=>{
                this.showContent=true;
            },450)
        },
        message(content,type){
            this.$message({
                message: content || '成功了！',
                type: type|| 'success'
            })
        }
    },
}
