<template>
    <div class="tab-wrap" >
            <div class="tab-nav">
                <draggable
                        v-model="tabDataSelf"
                        v-bind="dragOptions">
                    <transition-group type="transition" >
                        <div class="tab-item" v-for="element in tabDataSelf"
                             @click="handleTab(element)"
                             :class="[currentType===element.taskName?'active':'']"
                             :key="element.className">
                            {{element.taskName}}
                            <!--                        <i class="close el-icon-close item-close" @click.stop="remove(element)"></i>-->
                        </div>
                    </transition-group>
                </draggable>
            </div>
            <div class="tab-content">
                <template  v-for="item in tabDataSelf">
                    <editor-input v-show="currentType===item.taskName" v-for="info in item.paramInfoMap" style="margin-bottom: 15px;"
                                  :label="info.alias"
                                  @change="change"
                                  :required="true"
                                  :value.sync="info.value" ></editor-input>
                </template>
            </div>
    </div>

</template>

<script>
    import draggable from "vuedraggable";
    import EditorInput from '../business/EditorInput/'
    import {deepClone} from "../../utils/common";
    export default {
        name: "Tab",
        props:{
            tabData:{
                required:true,
                type:Array
            }
        },
        watch:{
            tabData:{
                handler(newVal,oldVal){
                  this.initData()
                },
                deep:true,
                immediate:true
            }
        },
        data(){
            return{
                drag:false,
                currentType:'',
                tabDataSelf:[],
            }
        },
        methods:{
            initData(){
                this.tabDataSelf = deepClone(this.tabData)
                if(this.tabDataSelf.length>0){
                    this.getActiveTaskName()
                }
            },
            getActiveTaskName(){
              let flag = false
              for(let i=0,l=this.tabDataSelf.length;i<l;i++){
                let item = this.tabDataSelf[i]
                if(item.taskName === this.currentType){
                  flag = true
                  this.currentType = item.taskName
                  break
                }
              }
              if(!flag){
                this.currentType = this.tabDataSelf[0].taskName
              }
            },
            change(){
                this.$emit('change',this.tabDataSelf)
            },
            changeBase(){},
            remove(){},
            handleTab(data){
                this.currentType = data.taskName
            }
        },
        computed:{
            dragOptions() {
                return {
                    animation: 200,
                    group: "description",
                    disabled: false,
                    ghostClass: "ghost"
                };
            }
        },
        components: {
            draggable,
            EditorInput
        },
    }
</script>

<style scoped lang="scss">
    .ghost {
        opacity: 0.5;
        background: #c8ebfb;
    }
    .tab-wrap{
        background: #fff;
        border: 1px solid #dcdfe6;
        box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
        padding: 5px 10px;
        box-sizing: border-box;
        border-radius: 5px;
        display: inline-flex;
        width: 100%;
        .tab-nav{
            /*width: 35%;*/
            /*max-width: 100px;*/
            display: inline-block;
            /*padding-right: 10px;*/
            box-sizing: border-box;
            /*border-right: 1px solid #cdcdcd;*/
            overflow-y: auto;
            max-height: 250px;
            .tab-item{
                height: 30px;
                text-align: center;
                list-style: none;
                line-height: 30px;
                cursor: move;
                /*border-radius: 5px;*/
                position: relative;
                font-size: 14px;
                color: #a8acac;
                /*margin: 10px 0 0;*/
                /*border: 1px solid #e9eaee;*/
                /*box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.12);*/
                /*padding: 0 2px;*/
                padding-right: 10px;
                box-sizing: border-box;
                border-right: 1px solid #e9eaee;
                &.active{
                    /*background: #3bc6a9;*/
                    color:#3bc6a9;
                    border-right: 1px solid #3bc6a9;
                }
            }

            .item-close {
                cursor: pointer;
                font-size: 20px;
                position: absolute;
                margin-top: -10px;
                top: 50%;
                right: 5px;
                color: #e5988c;
                border-radius: 3px;
                display: inline-block;
            }
        }
        .tab-content{
            /*width: 65%;*/
            min-width: 300px;
            display: inline-block;
        }
        .no-data{
            width: 100%;
            /* display: block; */
            text-align: center;
            line-height: 50px;
        }
    }

</style>
