<template>
    <div class="content-view">
        <div class="save-button">
            <el-button :type="isEdit?'primary':'info'"  :disabled="!isEdit" @click="save" size="small"
                       style="width: 100px">保存
            </el-button>
            <div class="update-remind" v-show="state===0">
              <span class="job-name-title">{{selfForm.name}}</span>
              <span class="remind" v-show="isEdit">注意：内容正在编辑，修改完成记得保存哟！</span>
            </div>
            <div class="add-remind" v-show="state===1"><span class="add-warn">注意：当前正在创建一个新的文件推送任务！</span></div>
        </div>
        <div class="content-mid-wrap">
            <div class="block">
                <div class="title">基础信息</div>
                <card :label="'监控基本信息'">
                    <editor-input style="margin-bottom: 10px;" :label="'监控名称'"
                                  @change="changeBase"
                                  :required="true"
                                  :value.sync="selfForm.name"></editor-input>
                    <editor-input style="margin-bottom: 10px;" :label="'文件路径'"
                                  :required="true"
                                  :value.sync="selfForm.path" @change="changeBase"></editor-input>
                    <div class="item-block">
                        <span style="margin-right: 8px"> <svg-icon icon-class="require" class="required"/>启用监控</span>
                        <el-switch
                                v-model="selfForm.enabled"
                                @change="changeBase"
                                :active-value="1"
                                :inactive-value="0"
                                active-color="#13ce66"
                                inactive-color="#dcdfe6">
                        </el-switch>
                    </div>
                    <div class="item-block">
                        <span style="margin-right: 8px"> <svg-icon icon-class="require" class="required"/>启用子文件夹监控</span>
                        <el-switch
                                v-model="selfForm.watchSubDirectories"
                                @change="changeBase"
                                :active-value="1"
                                :inactive-value="0"
                                active-color="#13ce66"
                                inactive-color="#dcdfe6">
                        </el-switch>
                    </div>
                    <div class="item-block">
                        <span style="margin-right: 8px"> <svg-icon icon-class="require" class="required"/>启用文件分发</span>
                        <el-switch
                                v-model="selfForm.mode"
                                @change="changeBase"
                                :active-value="1"
                                :inactive-value="0"
                                active-color="#13ce66"
                                inactive-color="#dcdfe6">
                        </el-switch>
                    </div>
                </card>
            </div>
            <div class="block" v-if="selfForm.mode===1">
                <div class="title">分发配置</div>
                <card :label="'分发配置'">
                    <div>
                        <span class="labelName"> <svg-icon icon-class="require" class="required"/>分发类型：</span>
                        <el-select multiple collapse-tags v-model="dispatchIds" @change="changeEspecial" style="width: 200px"
                                   placeholder="请选择" size="small">
                            <el-option
                                    v-for="item in dispatchList"
                                    :key="item.type"
                                    :label="item.name"
                                    :value="item.type">
                            </el-option>
                        </el-select>
                    </div>
                    <p class="labelName"> <svg-icon icon-class="require" class="required"/>文件类型：</p>
                    <div v-for="fileType in watcherDispatches"
                         style="margin-bottom: 5px;padding-left: 15px;box-sizing: border-box;font-size: 12px">
                        <span>{{fileType.dispatchName}}：</span>
                        <el-select v-model="fileType.dataConfigIdsArr" multiple collapse-tags style="width: 220px"
                                   @change="changeFile($event,fileType.dispatchType)"
                                   placeholder="请选择" size="mini">
                            <el-option
                                    v-for="item in fileList"
                                    :key="item.id"
                                    :label="item.alias"
                                    :value="item.id">
                            </el-option>
                        </el-select>
                    </div>
                </card>
            </div>
            <div class="block" v-else>
                <div class="title">过滤器</div>
                <card :label="'文件过滤配置'">
                    <editor-input style="margin-bottom: 15px;" :label="'文件类型过滤'"
                                  @change="changeBase"
                                  :required="false"
                                  :value.sync="selfForm.fileFilter"></editor-input>
                    <editor-input style="margin-bottom: 15px;" :label="'子文件类型过滤'"
                                  @change="changeBase"
                                  :required="false"
                                  :value.sync="selfForm.watchDirFilter"></editor-input>
                </card>
            </div>
            <div class="block">
                <div class="title">监控配置</div>
                <card :label="'事件配置'" style="width: 330px">
                    <div class="item-block">
                        <p class="labelName"> <svg-icon icon-class="require" class="required"/>exceptionHandler的实现类</p>
                        <el-select v-model="selfForm.exceptionHandlerClass" @change="changeBase" placeholder="请选择"
                                   size="small">
                            <el-option
                                    v-for="item in exceptionHandlerList"
                                    :key="item.name"
                                    :label="item.name"
                                    :value="item.className">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="item-block">
                        <p class="labelName"> <svg-icon icon-class="require" class="required"/>回调Listener的实现类</p>
                        <el-select v-model="selfForm.listenerClass" @change="changeBase" placeholder="请选择" size="small">
                            <el-option
                                    v-if="selfForm.mode===1"
                                    v-for="item in dispatchListenList"
                                    :key="item.name"
                                    :label="item.name"
                                    :value="item.className">
                            </el-option>
                            <el-option
                                    v-else
                                    v-for="item in filterListenList"
                                    :key="item.name"
                                    :label="item.name"
                                    :value="item.className">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="item-block">
                        <p class="labelName"> <svg-icon icon-class="require" class="not-required"/>开始回调类</p>
                        <el-select v-model="selfForm.startListenerClass" @change="changeBase" placeholder="请选择"
                                   size="small">
                            <el-option
                                    v-for="item in startListenerList"
                                    :key="item.name"
                                    :label="item.name"
                                    :value="item.className">
                            </el-option>
                        </el-select>
                    </div>
                </card>
            </div>
            <div class="block">
                <div class="title">监控相关事件</div>
                <card :label="'事件编辑'">
                    <p class="labelName"> <svg-icon icon-class="require" class="required"/>事件类型：</p>
                    <el-select v-model="eventIds" multiple placeholder="请选择" size="small" @change="changeBase">
                        <el-option
                                v-for="item in eventList"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </card>
            </div>
        </div>

    </div>
</template>

<script>
    const events = [
        {
            value: 1,
            label: '创建',
            bg: 'linear-gradient(0deg, rgb(5 156 108), rgb(8 199 129))',
        }, {
            value: 2,
            label: '删除',
            bg: 'linear-gradient(0deg, rgb(167 38 8), rgb(214 47 9))',
        },
        {
            value: 3,
            label: '修改',
            bg: 'linear-gradient(0deg,  rgb(181 128 6),rgb(212 179 7))',
        },
        {
            value: 4,
            label: '重命名',
            bg: 'linear-gradient(0deg,rgb(11,148,245),rgb(8,185,226))',
        }
    ]
    import {deepClone} from "../../../utils/common";
    import {contentMixin} from "../../../mixin/contentMixin";

    export default {
        name: "FileWatchContent",
        mixins:[contentMixin],
        data() {
            return {
                exceptionHandlerList: [],            // 异常处理
                dispatchList: [],                    // 分发类型
                dispatchListenList:[],               // 分发模式下的监控回调
                filterListenList:[],                 // 过滤器模式下的监控回调
                startListenerList: [],               // 开始回调类
                fileList: [],                        // 文件类型
                eventList: events,                   // 事件列表
                isEditBase: false,                    // 是否编辑基础信息、过滤器、事件
                isEditEspecial: false,                // 是否编辑了（分发分配、监控配置）
                dispatchIds: [],                     // 选中的分发类型id
                eventIds: [],                        // 选中的事件id
                selfForm: {},
                watcherDispatches: [],               // 分发和文件类型对象
                isFirstInit: true
            }
        },
        watch: {
            dispatchIds: {
                handler(newVal, oldVal) {
                    if(this.isFirstInit){ // 第一次赋值不操作(getDispatchIds()完成了赋值)
                        this.isFirstInit = false
                    }else{                // 后面切换分发类型，切换
                        this.setFiles(newVal,oldVal)
                    }
                },
                deep: true,
            },
            form: {
                handler(newVal, oldVal) {
                    this.initFirstData()
                    this.getDispatchIds()
                    this.getEventIds()
                },
                deep: true,
            },
        },
        created() {
            this.setData()
        },
        methods: {
            initEditData(){
                this.isEditBase=false;
                this.isEditEspecial=false;
                this.isEdit =false
            },
            initFirstData(){
                this.initEditData()
                this.isFirstInit = true;
                this.watcherDispatches = []
                this.selfForm = deepClone(this.form)
            },
            validateBase() {
               let flag =  this.selfForm.name === '' ||
                    this.selfForm.path === '' ||
                   this.selfForm.exceptionHandlerClass === '' ||
                   this.selfForm.listenerClass === ''||
                    this.selfForm.event === ''
                  return flag
            },
            validateEspecial() {
                return this.dispatchIds === [] ||
                    this.watcherDispatches === []
            },
            save() {
                if (this.isEditBase) {
                    this.saveBase()
                }
                if (this.isEditEspecial&&this.state===0) { // 编辑的时候才调这个接口
                    this.saveEspecial()
                }
            },
            saveBase() {
                this.formatRequestData()
                if (this.validateBase()) {
                    this.message('信息不能留空，请继续填写！', 'warning')
                    return
                }
                let url
                if (this.state === 0) {  // 编辑
                    url = 'watchConfig/updateConfig'
                } else {              // 创建
                    url = 'watchConfig/addConfig'
                    this.selfForm.watcherDispatches = this.watcherDispatches
                }
                this.request.postJson(url, this.selfForm)
                    .then(res => {
                        if (res.data.code === 200) {
                            this.message('保存成功！')
                            if (this.state === 1) {
                                this.$emit('handleAdd', res.data.data)
                            } else {
                                this.$emit('handleUpdate', res.data.data)
                            }
                            this.initEditData()
                        }
                    }).catch(err => {
                        this.initEditData()
                        this.message('保存失败！', 'error')
                })
            },
            saveEspecial() {
                if (this.validateEspecial()) {
                    this.message('信息不能留空，请继续填写！', 'warning')
                    return
                }
                let url = 'watchConfig/batchSaveWatcherDispatch'
                this.request.postJson(url,this.watcherDispatches)
                    .then(res => {
                        if (res.data.code === 200) {
                            this.message('保存成功！')
                            if (this.state === 1) {
                                this.$emit('handleAdd', res.data.data)
                            } else {
                                this.$emit('handleUpdate', res.data.data)
                            }
                            this.initEditData()
                        }
                    }).catch(err => {
                        this.initEditData()
                        this.message('保存失败！', 'error')
                })
            },
            getEventIds() {
                this.eventIds = []
                if (this.selfForm.event && this.selfForm.event.length > 0) {
                    this.eventIds = this.selfForm.event.split(',').map(Number)
                }
            },
            // 获取分发id和文件id
            getDispatchIds() {
                this.dispatchIds = []
                let _data = this.selfForm.watcherDispatches
                if (_data && _data.length > 0) {
                    this.watcherDispatches = deepClone(_data)
                    for (let i = 0, l = this.watcherDispatches.length; i < l; i++) {
                        let item = this.watcherDispatches[i]
                        this.dispatchIds.push(item.dispatchType)
                        item.dataConfigIdsArr = item.dataConfigIds.split(',').map(Number)
                    }
                }
            },
            formatRequestData() {
                this.setEvent()
            },
            setEvent() {
                this.selfForm.event = this.eventIds.join(',')
            },
            setFiles(newVal,oldVal) {
                if (newVal.length > oldVal.length) { // 新增
                    let diffId = this.getArrDif(newVal, oldVal)[0]
                    for (let i = 0, l = this.dispatchList.length; i < l; i++) {
                        let item = this.dispatchList[i]
                        if (item.type === diffId) {
                            this.watcherDispatches.push(
                                {
                                    dataConfigIds: '',
                                    dataConfigIdsArr: [],
                                    dispatchName: item.name,
                                    dispatchType: item.type,
                                    watcherConfigId: this.selfForm.id ? this.selfForm.id : '',
                                }
                            )
                            break;
                        }
                    }
                } else if (oldVal.length > newVal.length) { // 移除
                    let diffId = this.getArrDif(oldVal, newVal)[0]
                    for (let i = 0, l = this.watcherDispatches.length; i < l; i++) {
                        let item = this.watcherDispatches[i]
                        if (item.dispatchType === diffId) {
                            this.watcherDispatches.splice(i, 1)
                            break
                        }
                    }
                }
            },
            changeFile(data, id) {
                this.changeEspecial()
                for (let i = 0, l = this.watcherDispatches.length; i < l; i++) {
                    let item = this.watcherDispatches[i]
                    if (item.dispatchType === id) {
                        item.dataConfigIds = data.join(',')
                        item.dataConfigIdsArr = data
                        this.$forceUpdate()
                        break
                    }
                }
            },
            // 修改基础信息、过滤器、事件
            changeBase() {
                this.changeEditState()
                this.isEditBase = true
            },
            // 修改分发配置、回调配置
            changeEspecial() {
                this.changeEditState()
                this.isEditEspecial = true
            },
            setData() {
                //异常处理类
                this.getData('watchConfig/getExceptionHandlerList').then(data => {
                    this.exceptionHandlerList = data
                })
                // 分发类型
                this.getData('watchConfig/getDispatchList').then(data => {
                    for (let key in data) {
                        this.dispatchList.push(data[key])
                    }
                })
                // 回调监听类
                this.getData('watchConfig/getListenerList').then(data => {
                    for(let i=0,l = data.length;i<l;i++){
                        let item = data[i]
                        if(item.name === 'DispatcherListener'){
                            this.dispatchListenList.push(item)
                        }else{
                            this.filterListenList.push(item)
                        }
                    }
                })

                // 启动类
                this.getData('watchConfig/getStartListenerList').then(data => {
                    this.startListenerList = data
                })
                // 文件类型
                this.getData('watchConfig/listDataConfig').then(data => {
                    this.fileList = data
                })
            },
            getData(url) {
                return new Promise(((resolve, reject) => {
                    this.request.get(url)
                        .then(res => {
                            if (res.data.code === 200) {
                                resolve(res.data.data)
                            }
                        }).catch(err => {
                        console.log(err)
                        reject(err)
                    })
                }))
            },
            // 差集（array1相对array2所没有的）
            getArrDif(array1, array2) {
                return array1.filter(function (val) {
                    return array2.indexOf(val) === -1
                })
            },
        },
    }
</script>

<style scoped lang="scss">
    @import "../../../styles/views/recycleContent";
</style>
