<template>
    <div class="scroll-group-wrap">
        <slot></slot>
    </div>
</template>
<script>
    export default {
        itemKey: 'ScrollGroup',
        componentName: 'ScrollGroup',
        props: {
            value: {
                type: [Array, String, Number],
                default() {
                    return [];
                }
            },
            multiSelect:{               // 是否支持多选
                type: Boolean,
                default:false
            },
            enableDelete:{              // 是否允许删除
                type:Boolean,
                default:true
            }
        },

        data() {
            return {
                activeKeys: [].concat(this.value)
            };
        },
        watch:{
            value(value) {
                this.activeKeys = [].concat(value);
            }
        },
        provide() {
            return {
                scroller: this
            };
        },
        methods: {
            setActiveNames(activeKeys) {
                activeKeys = [].concat(activeKeys);
                let value = this.multiSelect ? activeKeys[0] : activeKeys;
                this.activeKeys = activeKeys;
                this.$emit('input', value);
                this.$emit('change', value);
            },
            handleItemClick(item) {
                if (this.multiSelect) {
                    let activeKeys = this.activeKeys.slice(0);
                    let index = activeKeys.indexOf(item.itemKey);
                    if (index > -1) {
                        activeKeys.splice(index, 1);
                    } else {
                        activeKeys.push(item.itemKey);
                    }
                    this.setActiveNames(activeKeys);
                } else {
                    // this.setActiveNames(
                    //     (this.activeKeys[0] || this.activeKeys[0] === 0) &&
                    //     this.activeKeys[0] === item.itemKey
                    //         ? '' : item.itemKey
                    // );
                    this.setActiveNames(item.itemKey);
                }
            },
            handleItemDelete(data){
                this.$emit('delete', data);
            }
        },

        created() {
            this.$on('item-click', this.handleItemClick);
            this.$on('item-delete', this.handleItemDelete);
        }
    };
</script>
<style scoped lang="scss">
    @import "../../styles/theme/index.scss";
    .scroll-group-wrap {
        height: 100%;
        width: 100%;
        min-width: 200px;
        overflow-y: auto;
        background: $scroll-list-bg;
        box-sizing: border-box;
    }
</style>
