<template>
    <recycle-scroller
            class="recycle-list-wrap"
            :items="data"
            :item-size="itemSize"
            :key-field="itemKey"
            v-slot="{ item }">
        <item :source="item" :itemKey="item[itemKey]" @handleClick="handleItemClick" @handleDel="handleDel">
            <slot :item="item"></slot>
        </item>
    </recycle-scroller>
</template>

<script>
    import "vue-virtual-scroller/dist/vue-virtual-scroller.css";
    import {RecycleScroller} from 'vue-virtual-scroller'
    import item from './recycleItem'

    export default {
        name: "RecycleView",
        data() {
            return {
                activeKeys:[].concat(this.value)
            }
        },
        props: {
            data: {
                require: true,
                type: Array
            },
            itemSize: {
                default: 80,
                type: Number
            },
            single: { // 是否单个操作
                type:Boolean,
                default:true
            },
            inverse:{ // 是否支持反选
                type:Boolean,
                default:false
            },
            value: {
                type: [Array, String, Number],
                default() {
                    return [];
                }
            },
            itemKey:{
                require:true,
                default:'id'
            }
        },
        watch: {
            value(value) {
                this.activeKeys = [].concat(value);
            }
        },
        provide() {
            return {
                scroller: this
            };
        },
        methods:{
            handleClick(data){
                this.$emit('handleSelect',data)
            },
            handleDel(data){
                this.$emit('handleDel',data)
            },
            setActiveNames(activeKeys) {
                activeKeys = [].concat(activeKeys);
                let value = this.single ? activeKeys[0] : activeKeys;
                this.activeKeys = activeKeys;
            },
            handleItemClick(item) {
                if (this.single) {
                    this.setActiveNames(
                        (this.activeKeys[0] || this.activeKeys[0] === 0) &&
                        (this.activeKeys[0] === item[this.itemKey])&&this.inverse
                            ? '' : item[this.itemKey]
                    );
                } else {
                    let activeKeys = this.activeKeys.slice(0);
                    let index = activeKeys.indexOf(item[this.itemKey]);

                    if (index > -1 ) {
                        activeKeys.splice(index, 1);
                    } else {
                        activeKeys.push(item[this.itemKey]);
                    }
                    this.setActiveNames(activeKeys);
                }
                this.$emit('handleSelect',item)
            }
        },
        components: {
            RecycleScroller,
            item
        }
    }
</script>

<style scoped lang="scss">
    .recycle-list-wrap {
        height: 100%;
        width: 100%;
        min-width: 200px;
        overflow-y: auto;
        padding: 10px 0;
        background: #142740;
        box-sizing: border-box;
        &::-webkit-scrollbar {
            display: none;
        }

        &.ready {
            .vue-recycle-scroller__item-view {
                color: #fff;
                text-align: center;
            }

        }
        .default-value{
            display: inline-block;
            width: 100%;
            height: 100%;
            border-bottom: 1px solid #ccc;
            color: #fff;
            text-align: center;
        }
        .legend-content {
            width: calc(100% - 250px);
            background: #F3F6FE;
            color: #fff;
            overflow-y: auto;
        }
    }
</style>
