<template>
    <div class="box-content">
        <el-popover
                placement="top"
                width="160"
                trigger="manual"
                v-model="visible">
            <p>确定新增任务？</p>
            <div style="text-align: right; margin: 0">
                <el-button size="mini" type="text" @click.stop.prevent="visible = false">取消</el-button>
                <el-button type="primary" size="mini" @click.stop.prevent="handleSuccess">确定</el-button>
            </div>
        </el-popover>
        <div class="state" :class="[data.enabled?'access':'forbid']" @click.stop.prevent="visible=true">{{index+1}}</div>
        <div class="value">
            <div class="title item">{{data.name}}</div>
            <div class="item"><span class="label">监控目录：</span>{{data.directoryName}}</div>
            <div><span class="event-name" v-for="value in data.eventList"
                       :style="{background: value.bg}">{{value.label}}</span>
            </div>
        </div>
    </div>
</template>

<script>
    import {itemMixin} from "../../../mixin/itemMixin";

    export default {
        name: "FileWatchItem",
        mixins:[itemMixin],
    }
</script>

<style scoped lang="scss">
    @import "../../../styles/views/recycleItem";
    .box-content{
        height: 100px;
    }
</style>
