<template>
    <div class="file-watch-wrap">
        <div style="width: 280px;">
            <add-comp @handleAdd="addFile" @handleInput="searchFile"> </add-comp>
            <div style="height: calc(100% - 60px)">
                <scroll-group v-model="active"  :enableDelete="true" @change="handleClick" @delete="handleDel">
                    <scroll-item v-for="(item,index) in fileData"
                               :itemKey="item.id"
                               :index="index"
                               :source="item" >
                        <file-watch-item :data="item" :index="index" @handleCopy="handleCopy"></file-watch-item>
                    </scroll-item>
                </scroll-group>
            </div>
        </div>
        <transition name="fade-transform" mode="out-in">
            <div class="content-view-wrap" v-show="showContent">
                <file-watch-content :form="currentViewData" :state="state" @handleAdd="handleAdd"
                              @handleUpdate="handleUpdate"></file-watch-content>
            </div>
        </transition>
        <el-dialog
                title="删除提示"
                :visible.sync="visibleDialog"
                width="30%">
            <span>确定删除该任务吗？</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="visibleDialog = false">取 消</el-button>
                <el-button type="primary" @click="deleteFile">确 定</el-button>
            </span>
        </el-dialog>
    </div>

</template>

<script>

    import {deepClone} from '../../../utils/common'

    import RecycleList from '../../../components/recycle-view/main'
    import AddComp from '../../../components/business/AddComp/AddComp'
    import ScrollGroup from '../../../components/Scroll/scroll-group'
    import ScrollItem from '../../../components/Scroll/scroll-item'
    import FileWatchItem from './item'
    import FileWatchContent from './content'

    import {fileMixin} from "../../../mixin/fileMixin";

     const events = [
        {
            value: 1,
            label: '创建',
            bg: 'linear-gradient(0deg, rgb(5 156 108), rgb(8 199 129))',
        }, {
            value: 2,
            label: '删除',
            bg: 'linear-gradient(0deg, rgb(167 38 8), rgb(214 47 9))',
        },
        {
            value: 3,
            label: '修改',
            bg: 'linear-gradient(0deg,  rgb(181 128 6),rgb(212 179 7))',
        },
        {
            value: 4,
            label: '重命名',
            bg: 'linear-gradient(0deg,rgb(11,148,245),rgb(8,185,226))',
        }
    ]


    const listUrl = 'watchConfig/listConfig'
    const deleteUrl = 'watchConfig/deleteConfig'
    export default {
        name: "FileWatch",
        mixins: [fileMixin],
        data() {
            return {
                listUrl: listUrl,
                deleteUrl: deleteUrl,
                // defaultFile: defaultFile,
                itemKey: 'id',
                index:1,
            }
        },
        mounted() {
            this.getFileList().then(res => {
                this.getEvents(res)
                this.fileData = res
                this.allFileData = res
                if(this.allFileData.length===0){
                  this.addFile()
                }
                this.setCurrentViewData()

            })
        },
        methods: {
            searchFile(value){
                this.fileData = []
                for(let i=0,l=this.allFileData.length;i<l;i++){
                    let item = this.allFileData[i]
                    let flag =  item.name.indexOf(value)>-1
                    if(flag){
                        this.fileData.push(item)
                    }
                }
            },
            addFile(){
               this.switchViewAnimate()
                this.currentViewData = deepClone({isUpdate:true})
                this.state = 1
            },
            handleAdd(data) {
                this.fileData.push(data)
                this.getEvents(this.fileData)
                this.state = 0
            },
            handleUpdate(data) {
                for (let i = 0, l = this.fileData.length; i < l; i++) {
                    let item = this.fileData[i]
                    if (item[this.itemKey] === data[this.itemKey]) {
                        this.fileData.splice(i, 1)
                        this.fileData.push(data)
                        this.getEvents(this.fileData)
                        break;
                    }
                }
            },
            // 重组事件数组
            getEvents(fileData) {
                for (let i = 0, l = fileData.length; i < l; i++) {
                    let item = fileData[i]
                    let arr = item.event.split(',').map(Number)
                    item.eventList = []
                    for (let k = 0, l = arr.length; k < l; k++) {
                        let value = arr[k]
                        for (let j = 0, l = events.length; j < l; j++) {
                            let event = events[j]
                            if (value == event.value) {
                                item.eventList.push(event)
                                break;
                            }
                        }
                    }
                }
            },
            handleCopy(data) {
                let _data = deepClone(data)
                _data.id = ''
                _data.name = ''
                _data.path = ''
                this.currentViewData = _data
                this.state = 1
                this.switchViewAnimate()
            },
        },
        components: {
            RecycleList,
            FileWatchContent,
            FileWatchItem,
            AddComp,
            ScrollItem,
            ScrollGroup
        }
    }
</script>

<style scoped lang="scss">
    @import "../../../styles/views/fileView";
</style>

