<template>
    <div class="box-content">
        <el-popover
                placement="top"
                width="160"
                trigger="manual"
                v-model="visible">
            <p>确定新增任务？</p>
            <div style="text-align: right; margin: 0">
                <el-button size="mini" type="text" @click.stop.prevent="visible = false">取消</el-button>
                <el-button type="primary" size="mini" @click.stop.prevent="handleSuccess">确定</el-button>
            </div>
        </el-popover>
        <div class="state" :class="[data.jobStatusCode===1?'access':'forbid']" @click.stop.prevent="visible=true">{{index+1}}</div>
        <div class="value">
            <div class=" item">
                <span class="fl title">{{data.alias}}（{{data.jobName}}）</span>
                <span class="fr  count" v-if="data.peddingCount">{{data.peddingCount}}</span></div>
            <div class="item"><span class="label">cron表达式：</span>{{data.cronExpression}}</div>
            <div class="item"><span class="label">下次执行时间：</span>{{data.nextFireTime |formDate}}</div>
            <div>
                <span class="job-button" @click.stop.prevent="handleJobBtn('1')" style="background:#3CA272">立即执行</span>
                <span class="job-button" v-show="data.jobStatusCode!==1" @click.stop.prevent="handleJobBtn('3')"
                      style="background: #91CB74">启动</span>
                <span class="job-button" v-show="data.jobStatusCode===1" @click.stop.prevent="handleJobBtn('4')"
                      style="background: #FC8452">暂停</span>
                <span class="job-button" v-show="data.status===1" @click.stop.prevent="handleJobBtn('2')"
                      style="background: #EE6666">停止</span>
            </div>
        </div>
    </div>
</template>

<script>
    import {itemMixin} from "../../../mixin/itemMixin";

    export default {
        name: "TaskItem",
        mixins: [itemMixin],
        data() {
            return {}
        },
        filters: {
            formDate(date) {
                if(date){
                    return new Date(date).format('MM-dd hh:mm:ss')
                }else{
                    return '--'
                }
            }
        },
        methods: {
            handleJobBtn(btnType) {
                switch (btnType) {
                    case "1":
                        this.doJob()
                        break;
                    case "2":
                        this.unDoJob()
                        break;
                    case "3":
                        this.startUpPushJob()
                        break;
                    case "4":
                        this.pausePushJob()
                        break;
                }
            },
            // 立即执行任务
            doJob() {
                let url = 'jobDetails/runJobNow'
                this.request.postJson(url, this.data)
                    .then(res => {
                        if (res.data.code === 200) {
                            this.message('执行完成！', 'success')
                            this.handleUpdate(res.data.data,['status','nextFireTime','peddingCount'])
                        }
                    }).catch(err => {
                    this.message('执行失败！', 'error')
                })
            },
            // 中断任务
            unDoJob() {
                let url = '/jobDetails/interruptJob'
                this.request.postJson(url, this.data)
                    .then(res => {
                        if (res.data.code === 200) {
                            this.message(res.data.message, 'success')
                            this.handleUpdate(res.data.data,['status'])
                        }
                    }).catch(err => {
                    this.message('任务中断失败！', 'error')
                })
            },
            // 启动推送任务
            startUpPushJob() {
                let url = '/jobDetails/resumeJob'
                this.request.postJson(url, this.data)
                    .then(res => {
                        if (res.data.code === 200) {
                            this.message(res.data.message, 'success')
                            this.handleUpdate(res.data.data,['jobStatusCode'])
                        }
                    }).catch(err => {
                    this.message('启动失败！', 'error')
                })
            },
            // 暂停推送
            pausePushJob() {
                let url = '/jobDetails/pauseJob'
                this.request.postJson(url, this.data)
                    .then(res => {
                        if (res.data.code === 200) {
                            this.message(res.data.message, 'success')
                            this.handleUpdate(res.data.data,['jobStatusCode'])
                        }
                    }).catch(err => {
                    this.message('任务中断失败！', 'error')
                })
            },
            handleUpdate(result,fields){
                for(let i=0,l=fields.length;i<l;i++){
                    let field = fields[i]
                    this.data[field] = result[field]
                }
                this.data.isUpdate = false
            },
        }
    }
</script>

<style scoped lang="scss">
    @import "../../../styles/views/recycleItem";
    .box-content {
        position: relative;
        .job-button {
            display: inline-block;
            background: #73C0DE;
            border-radius: 3px;
            padding: 0 5px;
            font-size: 12px;
            margin-right: 2px;
            color: #fff;
            cursor: pointer;
        }
        .count {
            background: #3abba2;
            border-radius: 50%;
            color: #fff;
            width: 20px;
            height: 20px;
            text-align: center;
            line-height: 20px;
            font-size: 12px;
        }
    }
</style>
