<template>
    <div class="box-wrap"  :class="[isActive?'active':'']">
        <div class="item-content"
             @click="handleClick"
             @mouseover="showDelete=true"
             @mouseout="showDelete=false">
            <slot></slot>
            <el-popconfirm
                    placement="bottom"
                    title="确定删除该任务吗？"
                    @onConfirm="handleDel"
                    @onCancel="visible = false"
                    width="200"
                    trigger="manual"
                    v-model="visible">
                <span class=" el-icon-close del"  v-show="showDelete" slot="reference" @click.stop="visible = !visible" ></span>
            </el-popconfirm>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'recycleItem',
        data(){
            return{
                showDelete:false,
                visible:false,
            }
        },
        props: {
            source: {
                type: Object,
                default() {
                    return {}
                }
            },
            itemKey:''
        },
        inject: ['scroller'],
        methods:{
            handleClick(){
                this.$emit('handleClick',this.source)
                this.$forceUpdate()
            },
            handleDel(){
                this.visible = false
                this.$emit('handleDel',this.source)
            },

        },
        computed:{
            isActive() {
                return this.scroller.activeKeys.indexOf(this.itemKey) > -1;
            }
        },
    }
</script>

<style scoped lang="scss">
    .box-wrap {
        width: 90%;
        margin: 0 auto 10px;
        border-radius: 5px;
        background: #28394F;
        cursor: pointer;
        color:#fff;
        border:1px solid #28394F;

        &.active{
            border:1px solid #0bf59a;
        }
        .legend-content {
            width: calc(100% - 300px);
            background: #7d7d7f;
        }

        .item-content {
            position: relative;
            display: flex;
            flex-wrap: wrap;
            box-sizing: border-box;
            min-height: 60px;
        }

        .type-name{
            background: #FDB44D;
            display: inline-block;
            width: 35px;
            height: 35px;
            border-radius: 50%;
            text-align: center;
            font-size: 12px;
            color: #fff;
            line-height: 35px;
            margin:20px 10px;
        }
        .item-name {
            /*width:calc(100% - 95px);*/
            color: #C8C8CA;
            text-align: left;
            margin:15px 0;
        }
        .item{
            line-height: 25px;
            font-size: 14px;
        }


        .item-value {
            display: flex;
            width: 100%;
            font-size: 12px;

            .value {
                color: #727478;
                font-size: 12px;
            }
        }
        .del{
            display: inline-block;
            width: 40px;
            height: 100%;
            background: #d20000;
            border-top-right-radius: 3px;
            border-bottom-right-radius: 3px;
            text-align: center;
            color: floralwhite;
            box-sizing: border-box;
            position: absolute;
            right: 0;
        }
        .el-icon-close{
            &:before{
                position: absolute;
                left: 50%;
                margin-left: -8px;
                top: 50%;
                margin-top: -8px;
            }
        }

    }
</style>
<style lang="scss">

</style>
