<template>
    <div class="logger-wrap" style="height: 300px;width: 600px">
        <DynamicScroller
                :items="datas"
                :min-item-size="54"
                class="recycle-list-wrap"
                :key-field="'index'"
                id="dynamicScroller">
            <template v-slot="{ item, index, active }">
                <DynamicScrollerItem
                        :item="item"
                        :active="active"
                        :size-dependencies="[ item.text]"
                        :data-index="index">
                    <div :class="color[item.type]" class="log">>> {{item.text}}</div>
                </DynamicScrollerItem>
            </template>
        </DynamicScroller>
    </div>

</template>
<script>
    import "vue-virtual-scroller/dist/vue-virtual-scroller.css";
    import {DynamicScroller, DynamicScrollerItem} from 'vue-virtual-scroller';

    let index = 0;
    const MAX_LENGTH = 500;
    let element;
    export default {
        name: 'Logger',
        components: {
            DynamicScroller,
            DynamicScrollerItem,
        },
        props: {
            loggerData: '',
            jobKey: ''
        },
        data() {
            return {
                datas: [],
                itemKey: 'jobKey',
                color: {
                    0: 'success',   // SUCCESS
                    1: 'info',   // INFO
                    2: 'warn',   // WARN
                    3: 'error',   // ERROR
                },
            }
        },
        watch: {
            loggerData(newVal) {
                newVal.index = index++;
                this.datas.push(newVal)
                if (this.datas.length > MAX_LENGTH) {
                    this.datas.splice(0, 1);
                }
                this.scrollToBottom();
            },
            jobKey(newVal) {
                this.datas = []
            }
        },
        mounted() {
            element = document.getElementById('dynamicScroller');
        },
        methods: {
            scrollToBottom() {
                if (element.scrollHeight > element.clientHeight) {
                    setTimeout(function () {
                        //设置滚动条到最底部
                        element.scrollTop = element.scrollHeight;
                    }, 0);
                }
            }
        }
    }
</script>
<style scoped lang="scss">
    .recycle-list-wrap {
        height: 100%;
        width: 100%;
        min-width: 200px;
        overflow-y: auto;
        /*background: #F6F7FB;*/
        box-sizing: border-box;
        /*&::-webkit-scrollbar {*/
        /*    display: none;*/
        /*}*/
    }


    .log {
        font-size: 16px;
    }

    .success {
        color: #02d255;
    }

    .error {
        color: #d72101;
    }

    .info {
        color: #01b1ab;
    }

    .warn {
        color: #e7ae02;
    }
</style>

