import Card from '../components/business/Card/'
import EditorInput from '../components/business/EditorInput/'

export const contentMixin = {
    data() {
        return {
            isEdit: false,                       // 是否正在编辑
        }
    },
    props: {
        form: {
            type: Object,
            require: true
        },
        state: 0,// 0 编辑 1 创建
    },
    methods: {
        save() {
            if (this.validate()) {
                this.message('信息不能留空，请继续填写！', 'warning')
                return
            }
            this.setRequestData()
            let url
            if (this.state === this.handleType['add']||this.state ===this.handleType['copy']) {  // 新增
                url = this.addUrl
            } else {
                url = this.updateUrl
            }
            this.request.postJson(url, this.selfForm)
                .then(res => {
                    if (res.data.code === 200) {
                        this.message('保存成功！')
                        if (this.state === this.handleType['add']||this.state ===this.handleType['copy']) {
                            this.$emit('handleAdd', res.data.data)
                        }else{
                            this.$emit('handleUpdate', res.data.data)
                        }
                        this.isEdit = false
                    }
                }).catch(err => {
                this.isEdit = false
                this.message('保存失败！', 'error')
            })
        },
        setRequestData(){},
        message(content, type) {
            this.$message({
                message: content || '成功了！',
                type: type || 'success'
            })
        },
        changeEditState(){
            this.isEdit=true
        },

    },
    components: {
        Card, EditorInput
    }
}

