<template>
    <div class="content-view">
        <div class="save-button">
            <el-button :type="isEdit?'primary':'info'"  :disabled="!isEdit" @click="save" size="small"
                       style="width: 100px">保存
            </el-button>
            <div class="update-remind" v-show="state===handleType['scan']">
              <span class="job-name-title">{{selfForm.alias}}({{selfForm.jobGroup}})</span>
              <span class="remind" v-show="isEdit">注意：内容正在编辑，修改完成记得保存哟！</span>
            </div>
            <div class="add-remind" v-show="state===handleType['add']"><span class="add-warn">注意：当前正在创建一个新的文件推送任务！</span></div>
        </div>
        <div class="content-mid-wrap">
            <div class="block">
                <div class="title">基础信息<svg-icon icon-class="require"  class="required"/></div>
                <card :label="'文件推送基本信息'">
                    <editor-input style="margin-bottom: 15px;" :label="'名称'"
                                  @change="changeEditState"
                                  :required="true"
                                  :disabled="state===handleType['scan']"
                                  :value.sync="selfForm.jobName"></editor-input>
                    <editor-input style="margin-bottom: 15px;" :label="'组名'"
                                  @change="changeEditState"
                                  :required="true"
                                  :disabled="state===handleType['scan']"
                                  :value.sync="selfForm.jobGroup"></editor-input>

                    <editor-input style="margin-bottom: 15px;" :label="'别名'"
                                  @change="changeEditState"
                                  :required="true"
                                  :value.sync="selfForm.alias"></editor-input>

                    <editor-input style="margin-bottom: 15px;" :label="'描述'"
                                  @change="changeEditState"
                                  :required="true"
                                  :value.sync="selfForm.description"></editor-input>

                    <div class="item-block">
                        <el-popover v-model="cronPopover">
                            <cron @change="changeCron" @close="cronPopover=false"></cron>
                            <editor-input style="margin-bottom: 15px;" :label="'cron表达式'"
                                          @change="changeCronValue"
                                          slot="reference"
                                          :required="true"
                                          :value.sync="selfForm.cronExpression"></editor-input>
                        </el-popover>
                    </div>
                </card>
            </div>
            <div class="block">
                <div class="title">任务设置</div>
                <card :label="'任务配置'">
                    <div>
                        <span class="labelName"> <svg-icon icon-class="require" class="required"/>任务类型：</span>
                        <el-select multiple collapse-tags v-model="currentJob"
                                   @change="changeEditState" placeholder="请选择">
                            <el-option-group
                                    v-for="group in AllJob"
                                    :key="group.value"
                                    :label="group.label">
                                <el-option
                                        v-for="(item,index) in group.options"
                                        :key="item.taskName+item.group"
                                        :label="item.taskName"
                                        :value="item.taskName+item.group">
                                </el-option>
                            </el-option-group>
                        </el-select>
                    </div>
                    <tab @change="changeTab" style="margin-top: 10px"
                         v-if="taskInfos&&taskInfos.length>0" :tab-data="taskInfos"></tab>
                </card>

            </div>
            <div class="block">
                <div class="title">任务日志</div>
                <card :label="'任务日志'" style="padding: 0">
                    <task-logger :jobKey="form.key" :loggerData="loggerData"></task-logger>
                </card>
            </div>
        </div>

    </div>
</template>

<script>
    import {contentMixin} from "../../../mixin/contentMixin";
    import {deepClone} from "../../../utils/common";
    import {handleType} from '../../dataConfig'

    import tab from '../../../components/Tab/tab'
    import taskLogger from '../../../components/Logger/TaskLogger';
    import {cron} from 'vue-cron';


    const updateUrl = 'jobDetails/updateJob'
    const addUrl = 'jobDetails/addJob'


    export default {
        name: "DispatchContent",
        mixins: [contentMixin],
        data() {
            return {
                updateUrl: updateUrl,
                addUrl: addUrl,
                extendedConfig: {},      // ftp 扩展配置
                selfForm: {},
                AllJob: [],
                jobList: [],
                currentJob: [],
                taskInfos: [],
                isFirstInit: true,
                cronPopover: false,
                visible: false,
                handleType:handleType
            }
        },
        props: {
            loggerData: null,
            state:handleType['scan']
        },
        watch: {
            form: {
                handler(newVal, oldVal) {
                    if(newVal.isUpdate){
                        this.initData()
                    }
                },
                deep: true,
            },
            currentJob: {
                handler(newVal, oldVal) {
                    if (this.isFirstInit) { // 第一次赋值不操作(getDispatchIds()完成了赋值)
                        this.isFirstInit = false
                    } else {                // 后面切换分发类型，切换
                        this.setTask(newVal, oldVal)
                    }
                },
            },
        },
        created() {
            this.getTasks()
        },
        mounted() {
            this.isEdit = false
        },
        methods: {
            changeTab(data){
                this.taskInfos = data
                this.changeEditState()
            },
            initData() {
                this.isEdit = false
                this.isFirstInit = true
                this.taskInfos = []
                this.selfForm = deepClone(this.form)
                if (this.selfForm.extendedConfig) {
                    this.extendedConfig = JSON.parse(this.selfForm.extendedConfig)
                }
                this.getTask()

            },
            changeCron(cron) {
                this.selfForm.cronExpression = cron
                this.changeEditState()
            },
            changeCronValue() {
                this.cronPopover = true
                this.changeEditState()
            },
            setTask(newVal, oldVal) {
                if (newVal.length > oldVal.length) { // 新增
                    let diffId = this.getArrDif(newVal, oldVal)[0]
                    for (let i = 0, l = this.jobList.length; i < l; i++) {
                        let item = this.jobList[i]
                        if (item.taskName+item.group === diffId) {
                            this.taskInfos.push(item)
                            break;
                        }
                    }
                } else if (oldVal.length > newVal.length) { // 移除
                    let diffId = this.getArrDif(oldVal, newVal)[0]
                    for (let i = 0, l = this.taskInfos.length; i < l; i++) {
                        let item = this.taskInfos[i]
                        if (item.taskName+item.group === diffId) {
                            this.taskInfos.splice(i, 1)
                            break
                        }
                    }
                }
            },
            getTask() {
                this.currentJob = []
                if (this.selfForm.taskInfos) {
                    for (let i = 0, l = this.selfForm.taskInfos.length; i < l; i++) {
                        let item = this.selfForm.taskInfos[i]
                        this.currentJob.push(item.taskName+item.group)
                    }
                    this.taskInfos = deepClone(this.selfForm.taskInfos)
                }
            },
            setRequestData() {
                if (this.selfForm.transmissionMode === 2) {
                    this.selfForm.extendedConfig = JSON.stringify(this.extendedConfig)
                }
                this.selfForm.taskInfos = deepClone(this.taskInfos)
            },
            validate() {
                return this.selfForm.jobName === '' ||
                    this.selfForm.jobGroup === '' ||
                    this.selfForm.description === '' ||
                    this.selfForm.cronExpression === '' ||
                    this.currentJob.length === 0
            },
            validateCopyData() {
                return this.selfForm.targetPath === ''
            },
            getTasks() {
                let url = 'jobDetails/getTasks'
                this.request.get(url)
                    .then(res => {
                        let data = res.data.data
                        let result = []
                        for (let key in data) {
                            result.push(
                                {
                                    label: key,
                                    value: key,
                                    options: data[key]
                                }
                            )
                            this.jobList = this.jobList.concat(data[key])
                        }
                        this.AllJob = result
                    }).catch(err => {
                    console.log(err)
                })
            },
            // 差集（array1相对array2所没有的）
            getArrDif(array1, array2) {
                return array1.filter(function (val) {
                    return array2.indexOf(val) === -1
                })
            },

        },
        components: {
            tab,
            cron,
            taskLogger
        }
    }
</script>

<style scoped lang="scss">
    @import "../../../styles/views/recycleContent";
</style>
