<template>
    <div class="content-view">
        <div class="save-button">
            <el-button :type="isEdit?'primary':'info'"  :disabled="!isEdit" @click="save" size="small"
                       style="width: 100px">保存
            </el-button>
            <div class="update-remind" v-show="state===0">
              <span class="job-name-title">{{form.alias}}({{form.name}})</span>
              <span class="remind" v-show="isEdit">注意：内容正在编辑，修改完成记得保存哟！</span>
            </div>
            <div class="add-remind" v-show="state===1"><span class="add-warn">注意：当前正在创建一个新的文件推送任务！</span></div>
        </div>
        <div class="content-mid-wrap">
            <div class="block">
                <div class="title">基础信息</div>
                <card :label="'文件配置基本信息'">
                    <editor-input style="margin-bottom: 15px;" :label="'名称(英文)'"
                                  @change="changeEditState"
                                  :required="true"
                                  :value.sync="form.name"></editor-input>
                    <editor-input style="margin-bottom: 15px;" :label="'别名(中文)'"
                                  @change="changeEditState"
                                  :required="true"
                                  :value.sync="form.alias"></editor-input>
                    <editor-input style="margin-bottom: 15px;" :label="'文件路径'"
                                  @change="changeEditState"
                                  :required="true"
                                  :value.sync="form.extend"></editor-input>
                    <editor-input style="margin-bottom: 15px;" :label="'文件匹配规则'"
                                  @change="changeEditState"
                                  :required="true"
                                  :value.sync="form.fileRegex"></editor-input>
                    <editor-input style="margin-bottom: 15px;" :label="'路径匹配规则'"
                                  @change="changeEditState"
                                  :required="false"
                                  :value.sync="form.pathRegex"></editor-input>
                </card>
            </div>
        </div>
    </div>
</template>

<script>
    // import {contentMixin} from "@/mixin/contentMixin";
    // import {deepClone} from "@/utils/common";

    import {contentMixin} from "../../../mixin/contentMixin";
    import {deepClone} from "../../../utils/common";

    const updateUrl = 'watchConfig/updateDataConfig'
    const addUrl = 'watchConfig/addDataConfig'

    export default {
        name: "FileConfigContent",
        mixins:[contentMixin],
        data(){
            return{
                updateUrl:updateUrl,
                addUrl:addUrl,
            }
        },
        watch:{
            form: {
                handler(newVal, oldVal) {
                    this.initData()
                },
                deep: true,
            },
        },
        methods:{
            initData(){
                this.selfForm = deepClone(this.form)
                this.isEdit =false
            },
            validate(){
              return  this.form.name ==='' ||
                    this.form.alias ==='' ||
                    this.form.extend ==='' ||
                    this.form.fileRegex===''
            },
            changeEditState(){
                this.isEdit=true
            },
        },
    }
</script>

<style scoped lang="scss">
    @import "../../../styles/views/recycleContent";

</style>
